import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import color from '../util/colors';

const styles = {
  root: {
    borderBottom: `10px solid ${color.FOOTER_BOTTOM}`,
    backgroundColor: color.FOOTER_TOP,
  },
  iconsContainer: {
    position: 'relative',
    display: 'inline-block',
    left: '50%',
    transform: 'translateX(-50%)',
  },
  iconContainer: {
    display: 'inline-block',
    textAlign: 'center',
    padding: '3px 20px',
    '& *': {
      boxSizing: 'content-box',
    },
  },
  a: {
    margin: '0 20px',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  icon: {
    transition: '.4s',
    borderRadius: '50%',
    border: '1px solid',
    borderColor: color.FOOTER_LINK,
    padding: 5,
    margin: '.5em',
    width: 24,
    height: 24,
    fill: color.FOOTER_LINK,
    '&:hover': {
      fill: color.FOOTER_LINK_ICON_ACTIVE,
      backgroundColor: color.FOOTER_LINK_ACTIVE,
      borderColor: color.FOOTER_LINK_ACTIVE,
    },
  },
};

const Footer = ({ classes }) => (
  <div className={classes.root}>
    <div className={classes.iconsContainer}>
      <div className={classes.iconContainer}>
        <a className={classes.a} href="https://github.com/ghajl/gol-gatsby" target="_blank" rel="noopener noreferrer">
          <svg className={classes.icon}>
            <path d="M12,2A10,10 0 0,0 2,12C2,16.42 4.87,20.17 8.84,21.5C9.34,21.58 9.5,21.27 9.5,21C9.5,20.77 9.5,20.14 9.5,19.31C6.73,19.91 6.14,17.97 6.14,17.97C5.68,16.81 5.03,16.5 5.03,16.5C4.12,15.88 5.1,15.9 5.1,15.9C6.1,15.97 6.63,16.93 6.63,16.93C7.5,18.45 8.97,18 9.54,17.76C9.63,17.11 9.89,16.67 10.17,16.42C7.95,16.17 5.62,15.31 5.62,11.5C5.62,10.39 6,9.5 6.65,8.79C6.55,8.54 6.2,7.5 6.75,6.15C6.75,6.15 7.59,5.88 9.5,7.17C10.29,6.95 11.15,6.84 12,6.84C12.85,6.84 13.71,6.95 14.5,7.17C16.41,5.88 17.25,6.15 17.25,6.15C17.8,7.5 17.45,8.54 17.35,8.79C18,9.5 18.38,10.39 18.38,11.5C18.38,15.32 16.04,16.16 13.81,16.41C14.17,16.72 14.5,17.33 14.5,18.26C14.5,19.6 14.5,20.68 14.5,21C14.5,21.27 14.66,21.59 15.17,21.5C19.14,20.16 22,16.42 22,12A10,10 0 0,0 12,2Z" />
          </svg>
        </a>
      </div>
      <div className={classes.iconContainer}>
        <a className={classes.a} href="mailto:michaelmsky@gmail.com?subject=Mail from GOL" target="_blank" rel="noopener noreferrer">
          <svg className={classes.icon}>
            <path d="M20,8L12,13L4,8V6L12,11L20,6M20,4H4C2.89,4 2,4.89 2,6V18A2,2 0 0,0 4,20H20A2,2 0 0,0 22,18V6C22,4.89 21.1,4 20,4Z" />
          </svg>
        </a>
      </div>
    </div>
  </div>
);

Footer.propTypes = {
  classes: PropTypes.shape({}).isRequired,
};

export default withStyles(styles)(Footer);
